.hero-section {
    position: relative;
    min-height: 70vh;
    padding-bottom: 1rem; /* Add consistent bottom padding */
    z-index: 0;
    overflow: visible; /* Allow the pseudo-element to extend beyond */
}

.hero-section > * {
    position: relative;
    z-index: 2;
}

.hero-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: calc(100% + 4rem); /* Extend beyond the parent's height */
    background: #ffce3e;
    border-radius: 0 0 700px 700px;
    z-index: 1;
}

/* Full Page Loading Spinner */
.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #fdf8f3;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    transition: opacity 0.3s ease-out;
}

.loading-spinner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.loading-logo {
    width: 300px;
    height: auto;
    animation: logoPulse 1.5s ease-in-out infinite;
}

.loading-text {
    font-size: 1.2rem;
    font-weight: 600;
    color: #0050c6;
    margin: 0;
    animation: textPulse 1.5s ease-in-out infinite;
}

@keyframes logoPulse {
    0%, 100% { 
        transform: scale(1);
        opacity: 0.8;
    }
    50% { 
        transform: scale(1.1);
        opacity: 1;
    }
}

@keyframes textPulse {
    0%, 100% { opacity: 0.7; }
    50% { opacity: 1; }
}

/* Hero image floating animation */
@keyframes floatDown {
    0% {
        transform: translateY(-100px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.hero-image {
    animation: floatDown 1.2s ease-out forwards;
}

/* Loading state - hide image until loaded */
.hero-image-loading {
    opacity: 0;
    transform: translateY(-100px);
    transition: none;
}

/* Optional: Add a subtle continuous floating effect after the initial animation */
@keyframes subtleFloat {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-10px);
    }
}

.hero-image-continuous-float {
    animation: floatDown 0.8s ease-out forwards, subtleFloat 3s ease-in-out infinite 0.8s;
    opacity: 1 !important; /* Override the inline opacity once animation starts */
}

/* Feature images scroll animations */
@keyframes featureFloatIn {
    0% {
        transform: translateY(60px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.feature-image {
    opacity: 0;
    transform: translateY(60px);
    transition: all 0.6s ease-out;
}

.feature-image.animate {
    animation: featureFloatIn 0.8s ease-out forwards;
}

/* Staggered animation delays for each feature */
.feature-image.animate:nth-child(1) {
    animation-delay: 0s;
}

.feature-image.animate:nth-child(2) {
    animation-delay: 0.2s;
}

.feature-image.animate:nth-child(3) {
    animation-delay: 0.4s;
}

.feature-image.animate:nth-child(4) {
    animation-delay: 0.6s;
}

/* Alternative: delay based on data attribute for more control */
.feature-image.animate[data-delay="0"] {
    animation-delay: 0s;
}

.feature-image.animate[data-delay="1"] {
    animation-delay: 0.2s;
}

.feature-image.animate[data-delay="2"] {
    animation-delay: 0.4s;
}

.feature-image.animate[data-delay="3"] {
    animation-delay: 0.6s;
}

/* Mobile screens */
@media (max-width: 768px) {
    .hero-section {
        min-height: 80vh;
        padding-bottom: 0rem; /* Adjusted padding for mobile */
    }
    
    .hero-section::before {
        height: calc(100% + 3rem); /* Slightly less extension on mobile */
    }

    @keyframes floatDown {
        0% {
            transform: translateY(-50px);
            opacity: 0;
        }
        100% {
            transform: translateY(0);
            opacity: 1;
        }
    }

    @keyframes featureFloatIn {
        0% {
            transform: translateY(30px);
            opacity: 0;
        }
        100% {
            transform: translateY(0);
            opacity: 1;
        }
    }
}

/* Small mobile screens */
@media (max-width: 480px) {
    .hero-section {
        min-height: 90vh;
        padding-bottom: 0rem;
    }
}

/* Tablet screens */
@media (min-width: 769px) and (max-width: 1024px) {
    .hero-section {
        min-height: 70vh;
        padding-bottom: 0rem;
    }
}